.slider {
    position: relative;
    width: 100%;
    // height: 100vh;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    display: flex;
    align-items: center;
    overflow: hidden;
}

.slide {
    position: relative;
    min-width: 100%;
    // height: 80%;
    transition: 0.5s;
    overflow: hidden;
}

%btn-styles {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 5%;
    height: 80%;
    background: none;
    outline: none;
    border: none;
    transition: 0.5s;
    &:hover {
        background: rgba(0, 0, 0, 0.356);
        cursor: pointer;
    }
}

#goLeft {
    left: 0;
    @extend %btn-styles;
}


#goRight {
    right: 0;
    @extend %btn-styles;
}